const ProgressBar = ({ progValue, isCounter = false }) => {
    if (isCounter && progValue < 50) return null;
    const strength = Math.floor(progValue);
    const color = strength < 50 ? (isCounter ? 'bg-warning' : 'bg-danger') : strength <= 75 ? 'bg-warning' : isCounter ? 'bg-danger' : 'warning';
    return (
        <div className="progress">
            <div className={`progress-bar ${color}`} style={{ width: `${progValue}%` }} />
        </div>
    );
};

export default ProgressBar;